@charset "utf-8";
/* 全局样式重置 */
/* 
	常用的变量（颜色、宽度、字体等放在这里面统一维护）,
	项目开始之前将设计稿中的基础颜色等总结出来放在此处
	作者：颜虎 Email:997948809@163.com
*/
/* 仅添加需要重置reset的签 ---start---*/
body,div,p,ul,li,a,dl,dt,dd,img,ol,input,table,tr,td,textarea,h1,h2,h3,h4,h5,h6,select {
  margin: 0;
  padding: 0;
  outline: none;
}
address, caption, cite, code, dfn, em, th, var, i, samp {
	font-style:normal;
	font-weight:normal;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display:block;
	*zoom:1;
}
q:before, q:after {
	content:'';
}
body,button,input,select,textarea {
	font:12px/1.5 SimSun, Helvetica, Tahoma, Arial, \5FAE\8F6F\96C5\9ED1, sans-serif;
	*line-height: 1.5;
	-ms-overflow-style: scrollbar;  
	color:#333;
	margin:0 auto;
	background:#fff;
}
/*禁止FF等浏览器给textarea增加可拖拽大小的设置*/
textarea {
  resize: none;
}
/*图片无边框*/
img {
  border: none;
  vertical-align: top;
}
input {
  border: 0;
  outline: none;
}
/*IE input X 去掉文本框的叉叉和密码输入框的眼睛图标*/
::-ms-clear, ::-ms-reveal{display: none;}
sup {
	vertical-align: text-top
}

sub {
	vertical-align: text-bottom
}
/* 清除列表样式设置，以下为示例，仅写你用到的标签 */
ul,li {
  list-style: none;
}
/*表格边框合并*/
table {
	border-collapse: collapse;
	border-spacing: 0
}
table,td,th,tr {
  border-collapse: collapse;
}
/*修复火狐按钮文字不垂直居中的bug*/
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
  border: none;
  padding: 0;
}

/* 仅添加需要重置reset的签 ---end---*/
@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?xi54ei');
  src:  url('../fonts/icomoon.eot?xi54ei#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?xi54ei') format('truetype'),
    url('../fonts/icomoon.woff?xi54ei') format('woff'),
    url('../fonts/icomoon.svg?xi54ei#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-cross:before {
  content: "\e900";
  color: red;
  font-size: 12px;
}
.icon-facebook2:before {
  content: "\ea91";
}
.icon-renren:before {
  content: "\ea99";
}
.icon-sina-weibo:before {
  content: "\ea9a";
}
.icon-github:before {
  content: "\eab0";
}
.icon-tumblr2:before {
  content: "\eaba";
}



